import { getRequestConfig } from 'next-intl/server';
import { routing } from './routing';

export default getRequestConfig(async ({ locale }) => {
  const supportedLocales: readonly ['en', 'ru'] = routing.locales ?? [
    'en',
    'ru',
  ];
  const selectedLocale: 'en' | 'ru' = supportedLocales.includes(
    locale as 'en' | 'ru'
  )
    ? (locale as 'en' | 'ru')
    : (routing.defaultLocale as 'en' | 'ru') || 'en';

  const messages = (await import(`../messages/${selectedLocale}.json`)).default;

  return {
    locale: selectedLocale,
    messages,
  };
});
